h3, p{
  display: flex;
}

.wordbreak{
  word-break:break-all;
}

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background-color: #b7d7e8;
}

.mainContainer{
  width: 60%;
  align-items: center;
  margin: auto;
  padding: 30px;
  background-color: #daebe8;
  display: flex;
}

main {
  flex: 1 0 auto;
}

footer{
  align-self: flex-end;
  height: 10%;
}

img{
  width: 100px;
  height: 250px;
}